import { Button, Rating, TextField } from '@mui/material'
import { useFormik } from 'formik'
import React from 'react'
import { SurveySchema } from '../ValidationSchemas'
import { enqueueSnackbar } from 'notistack'
import axios from 'axios'
const SurveyForm = () => {


    const onSubmit=async()=>{


        const response=await axios.post('http://localhost:8080/api/v1/submit',{...values});

       
            if(response.data.success){

                enqueueSnackbar('Form Submitted Successfully', {
                    variant: 'success',
                    anchorOrigin: {
                        vertical: 'top',
                        horizontal: 'right',
                    },
                    style: { backgroundColor: '#4caf50', color: '#fff' }
                });
                handleReset()
               
            }
       
    }

    const { values,handleBlur,handleChange,errors,touched,handleSubmit,handleReset } = useFormik({
        initialValues: {
            first_Name: '',
            last_Name: '',
            email: '',
            design_Rating: null,
            overAllRating: null,
            feedBack:''
        },
        validationSchema: SurveySchema,
        onSubmit

    })

   
    return (
        <div className='md:w-[80vw] w-[90vw] mt-20 md:mt-0  md:p-5 p-4 border shadow-lg shadow-gray-200 '>
            <form onSubmit={handleSubmit}>
                <h1 className='md:text-2xl text-xl uppercase text-center'>We Value Your Feedback !</h1>
                <h4 className=' text-center md:text-lg my-2'> Help Us Improve By Answering A Few Questions</h4>
                {/* this is row for firstname and full name  */}
                <div className=' grid md:grid-cols-2 gap-3 mt-4'>
                    <div>
                        <TextField onBlur={handleBlur} onChange={handleChange}  name='first_Name' value={values.first_Name} fullWidth id="outlined-basic" label="First Name" variant="outlined" placeholder='Enter Your First Name' />
                        {errors.first_Name && touched.first_Name && <p className=' text-red-500 text-sm pl-1'>{errors.first_Name}</p>}
                    </div>

                    <div>
                        <TextField name='last_Name' onBlur={handleBlur} onChange={handleChange} value={values.last_Name} fullWidth id="outlined-basic" label="Last Name" variant="outlined" placeholder='Enter Your Last Name' />
                        {errors.last_Name && touched.last_Name && <p className=' text-red-500 text-sm pl-1'>{errors.last_Name}</p>}
                    </div>
                </div>
                {/* this is input row  */}
                <div className='my-5'>
                    <TextField onBlur={handleBlur} onChange={handleChange} name='email' value={values.email} className='md:w-[60%] w-full' type='email' id="outlined-basic" label="Email" variant="outlined" placeholder='Enter Your Email' />
                    {errors.email && touched.email && <p className=' text-red-500 text-sm pl-1'>{errors.email}</p>}
                </div>
                {/* this is rating row  */}
                <div className='my-5'>
                    <h5 className='my-2'>Design And Layout Rating</h5>
                    <Rating onBlur={handleBlur} onChange={handleChange} name='design_Rating' value={values.design_Rating} className=' text-lg ml-2' />
                    {errors.design_Rating && touched.design_Rating && <p className=' text-red-500 text-sm pl-1'>{errors.design_Rating}</p>}
                </div>
                {/* this is rating row  */}
                <div className='my-5'>
                    <h5 className='my-2'>Over All User Experience</h5>
                    <Rating onBlur={handleBlur} onChange={handleChange} name='overAllRating' value={values.overAllRating} className=' text-lg ml-2' />
                    {errors.overAllRating && touched.overAllRating && <p className=' text-red-500 text-sm pl-1'>{errors.overAllRating}</p>}
                </div>  
                {/* this is input row  */}
                <div className='my-5'>
                    <TextField value={values.feedBack} name='feedBack' onBlur={handleBlur} onChange={handleChange} className='md:w-[60%] w-full' label="Your Feed Back" multiline minRows={4} />
                    {errors.feedBack && touched.feedBack && <p className=' text-red-500 text-sm pl-1'>{errors.feedBack}</p>}
                </div>

                {/* this is button row  */}
                <div>
                    <Button type='submit' variant="contained">Send FeedBack</Button>
                </div>

            </form>
        </div>
    )
}

export default SurveyForm
