import * as Yup from 'yup';


export const SurveySchema=Yup.object({
    first_Name:Yup.string().min(3).max(10).required('First Name is Required'),
    last_Name:Yup.string().min(3).max(10).required('Last Name is Required'),
    email:Yup.string().email().min(3).required('Email Name is Required'),
    design_Rating:Yup.number().required('Design Rating is Required'),
    overAllRating:Yup.number().required('OverAll Rating is Required'),
    feedBack:Yup.string().required('Feed Back is Required')
})